* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

img {
  margin-top: auto;
  margin-bottom: auto;
}

.item-country {
  display: flex;
}

.item-title {
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.card-img-top {
  display: flex;
}

.card-languages {
  display: flex;
}

.list-group {
  margin-left: 10px;
}

.card-title {
  margin: 0;
  margin-left: 20px;
}

.card-text {
  margin-bottom: 0;
}
